<template>
<div class="login">
  <label class="spacing">Pseudo</label>
  <input name="username" type="text" v-model="username">
  <label class="spacing">Mot de passe</label>
  <input name="password" type="password" v-model="password">
  <button class="btn btn-dark" type="submit" @click="connect()">Se connecter</button>
  <label class="spacing">Vous n'avez pas de compte? <router-link to="/register">Inscrivez vous.</router-link></label>
</div>
</template>

<script>
export default {
  name: 'LoginComp',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    connect (e) {
      return console.log(this.username + ' ' + this.password)
    }
  }
}
</script>

<style>
  .login{
    text-align: center;
    display: flex;
    flex-direction: column;
    margin-left: 15%;
    margin-right: 15%;
  }
  .spacing {
    margin-top: 1%;
    margin-bottom: 1%;
  }
  .button {
    margin-top: 20px;
  }
  .btn {
    margin-top: 3%;
    margin-bottom: 3%;
  }

</style>



